export const THRESHOLD = {
    E2: 8000.0,
    LH: 10.0,
    PROG: 2.0,
    FSH: 10.0,
    TSH: 2.5,
    PRL: 35.0,
    DHEAS: 300.0,
    BHCG: 5.0
};

export const useBloodTestUtil = () => {
    const isStyleRed = (key, value) => {
        if (Number(value) > THRESHOLD[key]) {
            return true;
        } else {
            return false;
        }
    };

    return {
        isStyleRed
    };
};
