        responsiveLayout="scroll"
        v-model:selection="selectedMonitorLine"
        selectionMode="single"
        dataKey="cm_lines_id"
        showGridlines
        class="p-datatable-sm p-mt-1 p-mb-2"
    >
        <p-column-group type="header">
            <p-row>
