                <p-column header="2" />
                <p-column header="3" />
                <p-column header="4" />
                <p-column header="5" />
                <p-column header="6" />
                <p-column header="7" />
                <p-column header="8" />
                <p-column header="9" />
                <p-column header="0" />
                <p-column header="1" />
                <p-column header="2" />
                <p-column header="3" />
                <p-column header="24>" />
                <p-column header="tot" />
                <p-column header="<10" />
                <p-column header="1" />
                <p-column header="2" />
                <p-column header="3" />
                <p-column header="4" />
                <p-column header="5" />
                <p-column header="6" />
                <p-column header="7" />
                <p-column header="8" />
                <p-column header="9" />
                <p-column header="0" />
                <p-column header="1" />
                <p-column header="2" />
                <p-column header="3" />
                <p-column header="24>" />
                <p-column header="ET" />
                <p-column header="E2" />
                <p-column header="LH" />
                <p-column header="PROG" />
                <p-column header="" />
            </p-row>
        </p-column-group>

        <p-column
            selectionMode="single"
            headerStyle="width: 3em"
            style="display: none"
        ></p-column>

        <p-column header="date" field="cycleDate" style="width: 80px">
            <template #body="{data}">
                {{ showFormatedDate(data.cycleDate.split(" ")[0]) }}
            </template>
        </p-column>
        <p-column header="cycle" field="cycleDay"> </p-column>
        <p-column header="inj" field="injectionDay"> </p-column>

        <p-column header="total" field="rTotal"> </p-column>
        <p-column header="<10" field="or10ls"> </p-column>
        <p-column header="1" field="or11"> </p-column>
        <p-column header="2" field="or12"> </p-column>
        <p-column header="3" field="or13"> </p-column>
        <p-column header="4" field="or14"> </p-column>
        <p-column header="5" field="or15"> </p-column>
        <p-column header="6" field="or16"> </p-column>
        <p-column header="7" field="or17"> </p-column>
        <p-column header="8" field="or18"> </p-column>
        <p-column header="9" field="or19"> </p-column>
        <p-column header="0" field="or20"> </p-column>
        <p-column header="1" field="or21"> </p-column>
        <p-column header="2" field="or22"> </p-column>
        <p-column header="3" field="or23"> </p-column>
        <p-column header="24>" field="or24gt"> </p-column>

        <p-column header="total" field="lTotal"> </p-column>
        <p-column header="<10" field="ol10ls"> </p-column>
        <p-column header="1" field="ol11"> </p-column>
        <p-column header="2" field="ol12"> </p-column>
        <p-column header="3" field="ol13"> </p-column>
        <p-column header="4" field="ol14"> </p-column>
        <p-column header="5" field="ol15"> </p-column>
        <p-column header="6" field="ol16"> </p-column>
        <p-column header="7" field="ol17"> </p-column>
        <p-column header="8" field="ol18"> </p-column>
        <p-column header="9" field="ol19"> </p-column>
        <p-column header="0" field="ol20"> </p-column>
        <p-column header="1" field="ol21"> </p-column>
        <p-column header="2" field="ol22"> </p-column>
        <p-column header="3" field="ol23"> </p-column>
        <p-column header="24>" field="ol24gt"> </p-column>

        <p-column field="etUs">
            <template #body="{data}">
                {{ data.etUs }}
            </template>
        </p-column>
        <p-column field="e2">
            <template #body="{data}">
                <span
                    :style="isStyleRed('E2', data.e2) ? { color: 'red' } : {}"
                >
                    {{ data.e2 }}
                </span>
            </template>
        </p-column>
        <p-column field="lh">
            <template #body="{data}">
                <span
                    :style="isStyleRed('LH', data.lg) ? { color: 'red' } : {}"
                >
                    {{ data.lg }}
                </span>
            </template>
        </p-column>
        <p-column field="prog">
            <template #body="{data}">
                <span
                    :style="
                        isStyleRed('PROG', data.prog) ? { color: 'red' } : {}
                    "
                >
                    {{ data.prog }}
                </span>
            </template>
        </p-column>

        <p-column field="gnt" />

        <template #footer>
            <p-button
                label="Edit Day"
                class="p-mr-2 p-button-success"
                @click="editDay"
                :disabled="!selectedMonitorLine"
                v-if="hasUpdatePermission"
                style="margin-left: -5px;"
            />
        </template>
    </p-data-table>
</template>

<script>
import { computed, onMounted, onUnmounted, ref, watch } from "vue";
import { emitter } from "@/event-bus";
import { usePermissionCheck } from "@/composable/users";
import { useBloodTestUtil } from "@/composable/blood-test-util";
import { showFormatedDate } from "@/service/utils";

export default {
    setup() {
        const store = useStore();

        const monitorLines = computed(() => store.state.cycleMonitoring.lines);
        const selectedMonitorLine = ref();

        const { hasUpdatePermission } = usePermissionCheck();

        const deselctLine = () => {
            selectedMonitorLine.value = null;
            store.commit("cycleMonitoring/SET_SELECTED_MONITOR_LINE", null);
        };

        const { isStyleRed } = useBloodTestUtil();

        watch(selectedMonitorLine, (newValue) => {
            store.commit("cycleMonitoring/SET_SELECTED_MONITOR_LINE", newValue);
        });

        onMounted(() => {
            emitter.on("cycle-monitoring:reload", deselctLine);
        });

        onUnmounted(() => {
            emitter.off("cycle-monitoring:reload", deselctLine);
        });

        return {
            monitorLines,
            selectedMonitorLine,
            editDay: () => {
                emitter.emit("cycle-monitoring-cycle-nav:edit-day", "edit");
            },
            hasUpdatePermission,
            isStyleRed,
            showFormatedDate
        };
    }
};
</script>

<style scoped>
:deep() .p-datatable-footer {
    background-color: white;
}

:deep() .p-datatable-footer {
    border-color: white;
}
</style>
