<template>
    <div class="comments p-mt-2" style="background-color: white">
        <div class="p-grid" style="border-bottom: 1px solid #dee2e6">
            <div class="p-col-fixed" style="width:100px">
                <span>Date</span>
            </div>
            <div class="p-col">
                <div>Comments</div>
            </div>
        </div>
        <div class="comment-item p-pt-1">
            <div
                class="p-grid p-pt-1"
                v-for="(item, index) in comments"
                :key="index"
                style="border-bottom: 1px solid #dee2e6"
                :style="styleObject(item)"
            >
                <div
                    class="p-col-fixed"
                    style="width:100px; font-size: 1.1rem;"
                >
                    {{ showFormatedDate(item.sDate) }}
                </div>
                <div class="p-col">
                    <p-textarea
                        v-model="item.comment"
                        rows="3"
                        style="width: 100%"
                        @focus="setSeletedCommnent(item)"
                        @input="setSeletedCommnent(item)"
                    />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { ref, computed, watch, reactive } from "vue";
import { useStore } from "vuex";
import { showFormatedDate } from "@/service/utils";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );
        const comments = ref([]);
        const form = reactive({
            lineId: null,
            comment: null
        });
        const styleObject = (item) => {
            if (
                store.state.cycleMonitoring.selectedComment &&
                item.line_id ==
                    store.state.cycleMonitoring.selectedComment.lineId
            ) {
                return {
                    backgroundColor: "#66FF66"
                };
            }
        };

        watch(monitorDetails, () => {
            comments.value = store.state.cycleMonitoring.comments;
        });

        const setSeletedCommnent = (data) => {
            store.commit("cycleMonitoring/SET_SELECTED_COMMMENT", {
                lineId: data.line_id,
                comment: data.comment
            });
        };

        return {
            comments,
            styleObject,
            setSeletedCommnent,
            showFormatedDate
        };
    }
};
</script>

<style scoped>
.comments {
    background-color: white;
    font-size: 1rem;
    font-family: "Roboto", BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
        Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
        "Segoe UI Symbol";
}
.comment-item {
    height: 200px;
    overflow-y: scroll;
    overflow-x: hidden;
}
.p-inputtextarea {
    font-size: 1rem;
}
.p-grid {
    margin-left: 0px;
    margin-right: 0px;
}
.p-col {
    padding: 0.5rem;
}
</style>
