<template>
    <p-dialog
        header="Cycle Monitorying"
        v-model:visible="cycleMonitoringDialog"
        :modal="true"
        style="width: 100%; height: 1000px; max-height: 95%;"
        class="p-inputtext-sm p-input-filled"
    >
        <main
            id="cycle-monitoriing-dialog"
            class="p-inputtext-sm p-input-filled"
        >
            <div v-if="hasNoCycle">
                <h1 style="color: red; text-align: left; margin-top: 100px;">
                    No Cycle Found
                </h1>
            </div>
            <div v-else>
                <div class="p-grid">
                    <div class="p-col-12 p-md-2">
                        <div style="padding: 0 10px;" class="">
                            <div>
                                <label for="cycle-type">
                                    <span class="cycle-info-label"
                                        >Cycle Type:</span
                                    >
                                    <strong class="p-ml-2">
                                        {{ cycle.cycleType }}
                                    </strong>
                                </label>
                            </div>
                            <div>
                                <label for="cycle-no">
                                    <span class="cycle-info-label"
                                        >Cycle #:</span
                                    >
                                    <strong class="p-ml-2">
                                        {{ cycle.cycleNo }}
                                    </strong>
                                </label>
                            </div>
                            <div>
                                <label for="cycle-start-date">
                                    <span class="cycle-info-label"
                                        >Cycle Start Date:</span
                                    >
                                    <strong class="p-ml-2 ">
                                        {{
                                            showFormatedDate(
                                                cycle.cycleStartDate
                                            )
                                        }}
                                    </strong>
                                </label>
                            </div>
                            <div>
                                <label for="cycle-cancelled">
                                    <span class="cycle-info-label"
                                        >Cycle Canceled:</span
                                    >
                                    <strong class="p-ml-2">
                                        {{
                                            cycle.cycleCancelled ? "Yes" : "No"
                                        }}
                                    </strong>
                                </label>
                            </div>
                            <div>
                                <label for="funded">
                                    <span class="cycle-info-label"
                                        >Funded :
                                    </span>
                                    <strong class="p-ml-2">
                                        {{ selectedCycle?.ohip ? "Yes" : "No" }}
                                    </strong>
                                </label>
                            </div>
                            <div>
                                <label class="p-mb-0">
                                    <span style="font-size: 14px">
                                        LMP Date
                                    </span>
                                </label>
                                <p-calendar
                                    v-model="cycle.lmpDate"
                                    dateFormat="mm/dd/y"
                                    :showIcon="true"
                                />
                            </div>
                            <div class="p-mt-1">
                                <label class="p-mb-0">
                                    <span style="font-size: 14px">
                                        Length in Days
                                    </span>
                                </label>
                                <p-input-text
                                    v-model="cycle.lengthInDays"
                                    style="width: 40px;"
                                />
                            </div>
                            <div class="p-mt-1">
                                <p-button
                                    label="Previous"
                                    icon="pi pi-chevron-left"
                                    class="p-mr-2 app-button p-button-sm"
                                    @click="previousCycle"
                                />
                                <p-button
                                    label="Next"
                                    icon="pi pi-chevron-right"
                                    iconPos="right"
                                    class="app-button p-button-sm"
                                    @click="nextCycle"
                                />
                            </div>
                        </div>
                    </div>

