                                        <label class="p-mr-2">Protocol</label>
                                        <p-dropdown
                                            v-model="cycle.stimProtocol2"
                                            :options="protocolOpts"
                                            optionLabel="label"
                                            optionValue="value"
                                        />
                                        <label class="p-mx-2 p-mb-0">
                                            <span style="font-size: 14px">
                                                Luteal Support
                                            </span>
                                        </label>
                                        <p-checkbox
                                            v-model="cycle.letSupport"
                                            :binary="true"
                                            style="margin-bottom: 4px;"
                                        />
