                                        </label>
                                        <div class="p-col">
                                            <p-input-text
                                                v-model="cycle.simDose1"
                                                style="width: 60px"
                                            />
                                        </div>
                                    </div>

                                    <div class="p-field p-grid">
                                        <label class="p-col-fixed p-mt-1"
                                            >Drug</label
                                        >
                                        <div class="p-col">
                                            <p-dropdown
                                                v-model="cycle.simDrug2"
                                                :options="stimulationDrugOpts"
                                                optionLabel="name"
                                                optionValue="name"
                                                style="width: 120px;"
                                            />
                                        </div>
                                        <label
                                            class="p-col-fixed"
                                            style="width: 40px"
                                        ></label>
                                        <div class="p-col">
                                            <p-input-text
                                                v-model="cycle.simDose2"
                                                style="width: 60px"
                                            />
                                        </div>
                                    </div>

