                                        label="Add"
                                        @click="addSimDrugs"
                                    />
                                </div>

                                <div
                                    class="p-col-fixed"
                                    style="width: 10px"
                                ></div>

                                <div class="p-col-fixed">
                                    <div class="p-field p-grid">
                                        <label class="p-col-fixed p-mt-1"
                                            >Drug</label
                                        >
                                        <div class="p-col">
                                            <p-dropdown
                                                v-model="cycle.letDrug1"
                                                :options="lutealDrugOpts"
                                                optionLabel="name"
                                                optionValue="name"
                                                style="width: 120px;"
                                            />
                                        </div>
                                        <label
                                            class="p-col-fixed p-mt-1"
                                            style="width: 40px"
                                        >
                                            Dose
                                        </label>
                                        <div class="p-col">
                                            <p-input-text
                                                v-model="cycle.letDose1"
                                                style="width: 60px"
                                            />
                                        </div>
                                    </div>

                                    <div class="p-field p-grid">
                                        <label class="p-col-fixed p-mt-1"
                                            >Drug</label
                                        >
                                        <div class="p-col">
                                            <p-dropdown
                                                v-model="cycle.letDrug2"
                                                :options="lutealDrugOpts"
                                                optionLabel="name"
                                                optionValue="name"
                                                style="width: 120px;"
                                            />
                                        </div>
                                        <label
                                            class="p-col-fixed"
                                            style="width: 40px"
                                        ></label>
                                        <div class="p-col">
