                                    <div class="p-field p-grid">
                                        <label class="p-col-fixed">2</label>
                                        <p-dropdown
                                            id="diag2"
                                            v-model="cycle.diag2"
                                            :options="diagnosisOptions"
                                            optionLabel="label"
                                            optionValue="value"
                                            class="p-col"
                                            style="max-width: 85%"
                                            @change="onChange"
                                            :showClear="true"
                                        />
