                                    </div>
                                    <div class="p-field p-grid">
                                        <p-input-text
                                            v-model="cycle.diagIssue1"
                                            class="p-mt-1"
                                            style="width: 100%; margin-bottom: 1px;"
                                        />

                                        <p-input-text
                                            v-model="cycle.diagIssue2"
                                            class="p-mt-1"
                                            style="width: 100%; margin-bottom: 1px;"
                                        />

                                        <p-input-text
                                            v-model="cycle.diagIssue3"
                                            class="p-mt-1"
