<template>
    <p-toolbar>
        <template #left>
            <p-button
                label="Save Comment"
                class="p-mr-6 p-button-info"
                @click="saveComment"
                :disabled="!selectedComment"
                v-if="hasUpdatePermission || hasCreatePermission"
            />
            <p-button
                label="New Day"
                class="p-mr-6 p-button-primary"
                @click="newDay()"
                v-if="hasUpdatePermission || hasCreatePermission"
            />
            <p-button
                label="Delete Day"
                class="p-mr-6 p-button-danger"
                @click="deleteCycleMonitorLine"
                v-if="hasDeletePermission"
            />
            <p-button
                label="Copy Day"
                class="p-mr-6 p-button-secondary"
                @click="copyDay"
            />
            <p-button
                label="Deletion Day"
                class="p-mr-6 p-button-danger"
                @click="deletedDay"
            />
            <p-button
                v-if="isNextBtn"
                label="Next"
                class="p-mr-6 p-button-primary"
                @click="next"
                :loading="nextBtnLoading"
            />
            <p-button
                label="PDF Report"
                class="p-mr-6 p-button-secondary"
                @click="showCycleMonitoringPrintPage"
            />
            <p-button
                label="Order Blood Test"
                class="p-mr-6 p-button-secondary"
                @click="orderBloodTest"
                :disabled="isCompltedCycle"
            />
        </template>
    </p-toolbar>

    <p-dialog
        header="Warning"
        v-model:visible="noPendingOrderDailog"
        :style="{ width: '300px' }"
        :modal="true"
    >
        <div class="p-d-flex p-ai-center">
            <i
                class="pi pi-exclamation-triangle p-mr-3"
                style="font-size: 2rem"
            />
            <strong>No Pending Test Order</strong>
        </div>

        <template #footer>
            <p-button
                label="Ok"
                icon="pi pi-times"
                @click="noPendingOrderDailog = false"
            />
        </template>
    </p-dialog>

    <p-dialog
        v-model:visible="newDayDialogvisible"
        modal
        header="Empty Day!!"
        :style="{ width: '30rem' }"
    >
        <p>
            Empty rows detected! Do you want to delete them?
        </p>
        <p-button label="Cancel" class="p-mr-2" @click="cancelNewDay" />
        <p-button
            label="Delete & Continue"
            class="p-button-danger"
            @click="deleteEmptyLines"
        />
    </p-dialog>
</template>

<script>
import { computed, inject, ref } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { CYCLE_MONITOR_LINE_DAILOG_TYPE, NL_FORM_BASE } from "@/constants";
import { usePermissionCheck } from "@/composable/users";
import { $axios } from "@/service/app-http";

export default {
    setup() {
        const newDayDialogvisible = ref(false);
        const emptyMonLines = ref([]);
        const cancelNewDay = () => {
            newDayDialogvisible.value = false;
        };

        const store = useStore();
        const selectedMonitorLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );
        const selectedComment = computed(
            () => store.state.cycleMonitoring.selectedComment
        );
        const disabled = ref(true);
        const isCycle = inject("isCycle");
        const deletedDayDialog = inject("deletedDayDialog");
        const isCompltedCycle = computed(
            () => store.getters["artCycle/isCompletedCycle"]
        );

        const orderBloodTestConfirmDialog = inject(
            "orderBloodTestConfirmDialog"
        );
        const orderBloodTestDialog = inject("orderBloodTestDialog");

        const saveComment = () => {
            store.dispatch("cycleMonitoring/updateComment").then(() => {
                emitter.emit(
                    "cycle-monitoring:reload",
                    store.state.artCycle.selectedCycle.cycleId
                );
            });
        };

        const deleteCycleMonitorLine = () => {
            emitter.emit("app:show-page-loading", true);
            store
                .dispatch("cycleMonitoring/deleteCycleMonitorLine")
                .then((data) => {
                    emitter.emit("app:show-page-loading", false);
                });
        };

        const deleteEmptyLines = () => {
            emitter.emit("app:show-page-loading", true);
            $axios
                .post("monitor/delete-empty-lines", emptyMonLines.value)
                .then((data) => {
                    emitter.emit("app:show-page-loading", false);
                    newDayDialogvisible.value = false;

                    store.dispatch(
                        "cycleMonitoring/getCycleMonitoringDetails",
                        store.state.artCycle.selectedCycle.cycleId
                    );

                    emitter.emit(
                        "cycle-monitoring-day-toolbar:new-day",
                        CYCLE_MONITOR_LINE_DAILOG_TYPE.NEW
                    );
                });
        };

        const isNextBtn = computed(() => {
            return (
                store.state.dailyReport.reportList.length != 0 &&
                store.state.dailyReport.selectedReport != null
            );
        });

        const noPendingOrderDailog = ref(false);
        const nextBtnLoading = ref(false);
        const next = () => {
            if (!store.getters["dailyReport/isAnyReportSelected"]) {
                return;
            }

            // const reportList =
            //     store.getters["dailyReport/getCurrentDoctorCheckedReportList"];

            const reportList =
                store.getters["dailyReport/getCheckedFilteredReportList"];

            const selectedIndex = reportList.findIndex((item) => {
                return item === store.state.dailyReport.selectedReport;
            });

            const nextItem = reportList[selectedIndex + 1];
            if (nextItem != undefined) {
                store.commit("dailyReport/SET_SELECTED_REPORT", nextItem);
            } else {
                noPendingOrderDailog.value = true;
                return;
            }
            nextBtnLoading.value = true;
            store
                .dispatch(
                    "artCycle/getCyclesDetails",
                    store.state.dailyReport.selectedReport.patientId
                )
                .then(() => {
                    return store.dispatch(
                        "cycleMonitoring/getCycleMonitoringDetails",
                        store.state.artCycle.selectedCycle.cycleId
                    );
                })
                .finally(() => {
                    nextBtnLoading.value = false;
                });
        };

        const {
            hasCreatePermission,
            hasUpdatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        return {
            deleteEmptyLines,
            cancelNewDay,
            newDayDialogvisible,
            disabled,
            selectedMonitorLine,
            selectedComment,
            isCompltedCycle,
            isNextBtn,
            noPendingOrderDailog,
            next,
            nextBtnLoading,
            copyDay: () => {
                emitter.emit(
                    "cycle-monitoring-day-toolbar:copy-day",
                    CYCLE_MONITOR_LINE_DAILOG_TYPE.COPY
                );
            },

            newDay: async () => {
                const { data } = await $axios.get(
                    `cycle/monitoring-empty-lines`,
                    {
                        params: {
                            cycleId: store.state.artCycle.selectedCycle.cycleId
                        }
                    }
                );
                // console.log(data);
                if (data.monitorLines.length > 0) {
                    newDayDialogvisible.value = true;
                    emptyMonLines.value = data;
                } else {
                    emitter.emit(
                        "cycle-monitoring-day-toolbar:new-day",
                        CYCLE_MONITOR_LINE_DAILOG_TYPE.NEW
                    );
                }
            },

            saveComment,
            showCycleMonitoringPrintPage: () => {
                const url =
                    NL_FORM_BASE +
                    "/cycles-reports/print-cycle-monitoring.php?cycleId=" +
                    store.state.artCycle.selectedCycle.cycleId +
                    "&patientId=" +
                    store.state.artCycle.selectedCycle.patientId;
                // "&apiToken=" +
                // store.state.user.accessToken;

                window.open(url);
            },
            deleteCycleMonitorLine,
            deletedDay: () => {
                deletedDayDialog.value = true;
            },
            orderBloodTest: () => {
                isCycle.value = 1;
                orderBloodTestDialog.value = true;
            },
            hasCreatePermission,
            hasUpdatePermission,
            hasDeletePermission
        };
    }
};
</script>

<style>
#cycle-monitoriing-dialog .p-toolbar {
    background-color: white;
}
</style>
